// ? Variables Globales
const d = document,
    $modal = new bootstrap.Modal(d.getElementById('modalReservas')), //*Seleccionando modal Bootstrap
    $btnAgregar = d.getElementById('btnAgregar'),
    $btnModificar = d.getElementById('btnModificar'),
    $btnEliminar = d.getElementById('btnEliminar'),
    $btnCancelar = d.getElementById('btnCancelar')


//TODO: Mostrando Calendario y Configurandolo
d.addEventListener('DOMContentLoaded', function() {
  var calendarEl = d.getElementById('calendar');

  var calendar = new FullCalendar.Calendar(calendarEl, {
    initialView: 'dayGridMonth',
    locale: 'es', //* Cambiando idioma del calendario a espaÃ±ol
    headerToolbar: { //? Botones de encabezados
      start: 'title',
      center: 'dayGridMonth,dayGridWeek,dayGridDay',
      end: 'today,prevYear,prev,next,nextYear'
    },
    buttonText: { //?Texto de los botones del encabezado
      today: 'Hoy',
      month: 'Mes',
      week: 'Semana',
      day: 'Dia',
    },
    dateClick: function(info){ // *Obteniendo datos al dar click en una fecha del calendario
      console.log('Fecha info:',info);
      console.log('Fecha info:',info.date);

      $btnAgregar.disabled = false;
      $btnModificar.disabled = true;
      $btnEliminar.disabled = true;

      // $('#fecha').val(date.format()); // Agregando fecha actual al input de texto de fecha

      $modal.show(); // Abriendo modal

    },
    events: 'https://dammtour.cl/dammtour/reservas.php',
    eventClick: function(info){ // *Evento al dar click en una reserva de una fecha
      info.el.style.cursor = 'pointer';

      $btnAgregar.disabled = true;
      $btnModificar.disabled = false;
      $btnEliminar.disabled = false;

      $modal.show();
    }  


  });
  calendar.render(); //* Renderizando calendario
});


// TODO: Evento al dar click en el boton de cancelar
$btnCancelar.addEventListener('click',e => {
  $modal.hide(); // Cerrando modal
});

//TODO: Activando reloj en el input de la hora
$('.clockpicker').clockpicker(); 






